.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.header {
  font-family: 'Roboto Slab', serif;
  font-weight: 500;
  font-size: 150px;
}

.kolba {
  width: 12px;
  height: 72px;
  border: 5px solid black;
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
  position: relative;
  top: 172px;
  left: 5px;
  background: linear-gradient(to top, yellow 70%, white 0%); 
}

.hr {
  width: 30px;
  height: 5px;
  background-color: black;
  position: relative;
  top: 176px;
  left: .7px;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-image: url('./components/Colba_colorful.jpg');
  background-position: center;
  background-size: cover;
  min-height: 100vh;
}

.subcontainer {
  display: flex;
  justify-content: center;
}

.image {
  width: 100px;
  height: 100px;
}

.cork {
  border-top: 18px solid black;
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  height: 0;
  width: 10px;
  position: relative;
  top: 150px;
  left: 438px;
}

.subtext {
 position: relative;
 bottom: 120px;
 font-weight: 700;
 font-size: 60px;
 color: gray;
}

.login-form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 400px;
  width: 300px;
  border: 1px solid black;
  background-color: white;
  border-radius: 5px;
  font-family: 'Roboto Slab', serif;
}

input[type=text],
input[type=password] {
  height: 30px;
  width: 200px;
  padding-left: 10px;
  border: 0.5px solid gray;
  border-radius: 5px;
}

button {
  height: 30px;
  width: 100px;
  border-radius: 5px;
  background-color: mediumspringgreen;
  transition: 0.3s all ease-in-out;
  font-weight: 500;
}

button:hover {
  background-color: plum;
}

.lead-board {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 400px;
  width: 700px;
  border: 1px solid black;
  background-color: white;
  border-radius: 5px;
  font-family: 'Roboto Slab', serif;
}

.alert {
  margin-top: 25px;
  color: red;
}